<template>
  <div class="pb-12">
    <div class="cover-photo">
      <div class="parallax-img svg-background" :style="parallax">
        <div class="abs-header pa-12 d-flex flex-column">
          <h2 class="overline mb-3">Github API</h2>
          <h2 class="overline mb-3">parallax scroll</h2>
          <h2 class="overline mb-3">infinite scroll</h2>
          <h1 style="text-transform:uppercase">Interview project for IU</h1>
        </div>
      </div>
    </div>
    <div id="repos-search" class="pa-4 px-12">
      <h1 class="py-4">Github repos search</h1>
      <v-text-field
        class="mt-4"
        label="Search by user name"
        outlined
        v-model="inputUser"
        append-icon="fas fa-search"
        @click:append="searchUser(inputUser)"
        @keydown="inputkeyDown"
        id="search-input"
      ></v-text-field>

      <v-list color="rgba(0,0,0,0)">
        <v-list-item
          v-for="(result,index) in searchResults"
          :key="index"
          @click="selectUser(result.login)"
        >
          <v-avatar class="ma-2">
            <img :src="result.avatar_url" alt="avartar" />
          </v-avatar>
          <v-list-item-title>{{ result.login }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </div>
    <v-divider></v-divider>
    <div id="repos-result" class="pa-4 px-12">
      <h1 class="py-4" v-show="searchedUser">{{searchedUser}}'s repos</h1>
      <v-select
        outlined
        :value="sortMethods[0]"
        :items="sortMethods"
        label="Sort method"
        @change="sort"
      ></v-select>
      <v-card class="my-5 pa-4" v-for="repo in repos">
        <v-card-title>{{repo.name}}</v-card-title>
        <v-card-text>{{repo.description}}</v-card-text>
        <v-card-actions class="d-flex justify-end">
          <a :href="repo.html_url" target="_BLANK">{{repo.html_url}}</a>
        </v-card-actions>
      </v-card>
      <div class="d-flex justify-center">
        <v-btn
          @click="loadMoreRepos"
          v-if="links && links.next"
          class="caption mt-12"
          outlined
          text
          :ripple="true"
          :loading="isLoading"
          :disabled="isLoading"
        >show more</v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  mounted() {
    window.addEventListener("scroll", e => {
      this.scrollY = window.scrollY;
    });

    // init repos
    this.searchUser();
    this.selectUser("bubblecigar");
  },
  data() {
    return {
      inputUser: "bubblecigar",
      page: 1,
      perpage: 2,
      sortMethods: ["created", "updated", "pushed", "full_name"],
      scrollY: 0
    };
  },
  computed: {
    ...mapState([
      "repos",
      "searchResults",
      "links",
      "searchedUser",
      "isLoading"
    ]),
    parallax() {
      return `transform: translateY(${this.scrollY * 0.6}px);`;
    }
  },
  methods: {
    inputkeyDown(e) {
      if (e.keyCode === 13) {
        this.searchUser();
      }
    },
    goToPage(url) {
      window.open(url);
    },
    searchUser() {
      this.$store.dispatch("searchUser", {
        user: this.inputUser,
        page: this.page,
        perpage: 10
      });
    },
    getReposByUser() {
      this.$store.dispatch("getReposByUser", {
        user: this.inputUser,
        page: this.page,
        perpage: this.perpage
      });
    },
    selectUser(user) {
      this.$store.commit("CLEAR_REPOS");
      this.$store.dispatch("selectUser", { user });
    },
    loadMoreRepos() {
      this.$store.dispatch("loadMoreRepos");
    },
    sort(e) {
      this.$store.commit("SET_SORT_METHOD", e);
      this.$store.dispatch("reSort");
    }
  }
};
</script>

